.App {
  font-family: sans-serif;
}

.content {
  display: flex;
  flex-direction: row;
  gap: 40px;
}

.nav {
  display: flex;
  background-color: #101010;
  color: aliceblue;
  align-items: center;
  justify-content: center;
  height: 65px;
}

.cap {
  display: flex;
  flex-direction: row;
  gap: 15px;
  padding: 2rem;
}

.cap-main {
  display: flex;
  flex-direction: column;
  gap: 5px;
  padding-top: 0.2rem;
}

.cap-main label {
  font-size: 14px;
}

.cap-main textarea {
  resize: none;
  font-size: 20px;
  padding: 0.5rem;
  border-radius: 5px;
  background-color: #e1ebee;
}

.tim {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.tim-main {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin: 0;
}

.tim-main input {
  width: 60px;
  height: 30px;
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid black;
  background-color: #e1ebee;
}

.tim-main label {
  font-size: 13px;
}

.tim-main button {
  padding: 0.4rem;
  width: 60px;
  border-radius: 5px;
}

button:hover {
  background-color: lightblue;
}

.note {
  padding: 1rem;
  font-size: 12px;
  font-weight: bold;
}
